import cuCustom from '@/colorui/components/cu-custom.vue'
export default {
	components: {
		cuCustom
	},
	data() {
		return {
			host: "http://47.104.29.236:9999/api/v1",
			// host: "http://127.0.0.1:9999/api/v1",
			menu_id: 0,
			list: [],
			win_id: 0,
			user_id: 0,
			stusersid: 0,
			care_num: 0
		};
	},
	onLoad(option) {
		this.menu_id = option.id;
		let that = this;
		// 获取到用户id
		uni.getStorage({
			key: 'user_id',
			success: function(res) {
				that.user_id = res.data;
			}
		});

		uni.showLoading({
			title: '加载中...',
			mask: true
		});
	},
	onReady() {
		uni.hideLoading()
	},
	methods: {
		// 获取的到菜品
		getmenu() {
			uni.request({
				url: `${this.host}/list_st/${this.menu_id}`,
				data: "",
				success: (data) => {
					this.list = data.data.data[0];
				}
			})
		},
		// 获取到商铺用户id
		getStusersId() {
			// 获取到窗口id
			let id = uni.getStorageSync('winid')
			this.win_id = id.win_id
			// console.log(this.win_id)
			uni.request({
				url: `${this.host}/stusers_id`,
				data: {
					win_id: this.win_id
				},
				success: (data) => {
					console.log(data.data)
					this.stusersid = data.data.data[0].stuser_id
				}
			})
		},
		// 点击购买添加到购物车
		buycare() {
			this.getStusersId()
			// console.log(this.stusersid);
			// console.log(this.menu_id);
			// console.log(this.user_id);
			uni.request({
				url: `${this.host}/add_care`,
				method: 'post',
				data: {
					user_id: this.user_id,
					menu_id: this.menu_id,
					stusersid: this.stusersid
				},
				success: () => {
					this.getcare();
				}
			})

		},
		// 获取到购物车中的数据数量
		getcare() {
			uni.request({
				url: `${this.host}/care_st`,
				data: {
					user_id: this.user_id
				},
				success: (res) => {
					this.care_num = res.data.data[0].care_num
				}
			})
		},
		// 点击购物车进行跳转
		clickcare() {
			let CurrentPages = getCurrentPages()
			// console.log(`${CurrentPages[0].route}`);
			uni.reLaunch({
				url: `/pages/meituan/myCart?win_id=${this.win_id}&path=${CurrentPages[0].route}`
			});
		}
	},
	onShow() {
		this.getmenu();
		this.getStusersId();
		this.getcare();
	}
}
